import React from "react";
import { Link } from "react-router-dom";
import QRCode from 'react-qr-code';
const AttendanceSetting = () => {

    const qrValue = 'https://10.0.0.168/hrmscloneproject/api/AttendanceSession/GetEmployeeAttendanceSummary';

    return (
        <>
            <div className="header">
                <div className="left-corner">
                    <i className="user-icon bi bi-clock"></i>
                    <div>Attendance</div>
                </div>
                <div className="tabs">
                    <div className="tabs-bar">
                        <div className="tabs-nav-container">
                            <span className="tabs-tab-prev">
                                <span className="tabs-tab-prev-icon">
                                    <i className="tabs-tab-prev-icon-target"></i>
                                </span>
                            </span>
                            <span className="tabs-tab-next">
                                <span className="tabs-tab-next-icon">
                                    <i className="tabs-tab-next-icon-target"></i>
                                </span>
                            </span>
                            <div className="tabs-nav-wrap">
                                <div className="tabs-nav-scroll">
                                    <div className="tabs-nav">
                                        <div className="tabs-tab">
                                            <div className="hidden-div">
                                                <Link
                                                    to="/adminattendance"
                                                    style={{
                                                        lineHeight: "55px",
                                                        display: "inline-block",
                                                    }}
                                                >
                                                    MY ATTENDANCE
                                                </Link>
                                            </div>
                                        </div>
                                        <div className="tabs-tab">
                                            <div className="hidden-div">
                                                <Link
                                                    to="/adminteamattendance"
                                                    style={{
                                                        lineHeight: "55px",
                                                        display: "inline-block",
                                                    }}
                                                >
                                                    TEAM ATTENDANCE
                                                </Link>
                                            </div>
                                        </div>
                                        <div className="tabs-tab">
                                            <div className="hidden-div">
                                                <Link
                                                    to="/adminempattendance"
                                                    style={{
                                                        lineHeight: "55px",
                                                        display: "inline-block",
                                                    }}
                                                >
                                                    EMPLOYEE ATTENDANCE
                                                </Link>
                                            </div>
                                        </div>
                                        <div className="tabs-tab">
                                            <div className="hidden-div">
                                                <Link  className="anchor"
                                                    
                                                    style={{
                                                        lineHeight: "55px",
                                                        display: "inline-block",
                                                    }}
                                                >
                                                    SETTINGS
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="Attendance-setting-main">
                <div className="setting-container">

                    <div className="sidesetting">
                        <h4 className="settingheading">Settings</h4>
                        <div style={{ flexDirection: "column" }}>
                            <div className="setting-item">QR Code</div>
                        </div>
                        <div>


                        </div>
                    </div>
                    <div className="qrmainsetting">
                        <QRCode
                            value={qrValue}
                            size={168} // Set the size of the QR code
                            level="L" // Set error correction level (L, M, Q, H)
                            fgColor="#000000" // Set the foreground color
                            bgColor="#ffffff" // Set the background color
                        />
                    </div>

                </div>
            </div>

        </>


    )
}

export default AttendanceSetting;